/* eslint-disable no-console */
/* eslint-disable max-classes-per-file */
import * as fs from 'node:fs';

import {
  ListMetaData,
  SambaClient,
  SambaConfig as BasicSambaConfig,
} from '@xip-online-data/samba-client';
import { v4 as uuid } from 'uuid';

import {
  ActiveFileHandlerInterface,
  FileHandlerConfig,
  FileHandlerInterface,
} from './file-handler.interface';
import { FileInfo, FileInfoEnum } from './types';

export interface SambaConfig extends BasicSambaConfig, FileHandlerConfig {
  type: 'samba';
}

const mapFileInfo = (fileInfo: ListMetaData): FileInfo => {
  return {
    name: fileInfo.name,
    size: fileInfo.size,
    modifyTime: fileInfo.modifyTime,
    type: fileInfo.type === 'D' ? FileInfoEnum.DIRECTORY : FileInfoEnum.FILE,
  };
};

export class SambaFile implements ActiveFileHandlerInterface {
  readonly #file: Buffer;

  readonly #tempFile: string;

  constructor(file: Buffer, tempFile: string) {
    this.#file = file;
    this.#tempFile = tempFile;
  }

  get(): Buffer {
    return this.#file;
  }

  close(): boolean {
    let success = false;
    fs.unlink(this.#tempFile, (err) => {
      success = err === null;
    });
    return success;
  }
}

export class SambaFilehandler implements FileHandlerInterface {
  private sambaClient: SambaClient;

  constructor(private readonly sambaConfig: SambaConfig) {
    console.log('Samba File handler setup!');
    fs.mkdirSync(sambaConfig.tmpDirectory, { recursive: true });
    this.sambaClient = new SambaClient(sambaConfig);
  }

  async init(): Promise<void> {}

  async list(dir: string): Promise<Array<FileInfo>> {
    const list = await this.sambaClient.list(dir);
    // Filter out the current and parent directory. They are not needed.
    const directories = list.filter(
      (r) => !(r.type === 'D' && (r.name === '.' || r.name === '..')),
    );
    return directories.map(mapFileInfo);
  }

  async readFile(remoteFile: string): Promise<ActiveFileHandlerInterface> {
    const tempFilename = `${uuid()}.tmp`;
    const localFile = `${this.sambaConfig.tmpDirectory}/${tempFilename}`;
    await this.sambaClient.getFile(remoteFile, localFile);
    const buffer = fs.readFileSync(localFile);
    return new SambaFile(buffer, localFile);
  }

  async writeFile(
    data: ActiveFileHandlerInterface,
    remotePath: string,
    filename: string,
  ): Promise<boolean> {
    console.log(
      `Writing file ${filename} to ${remotePath}, checking if directory exists`,
    );
    let directoryExists = false;
    try {
      console.log(`Checking if ${remotePath} exists`);
      const exist = await this.sambaClient.fileExists(remotePath);
      directoryExists = exist;
    } catch (e) {
      console.error(e);
    }

    if (!directoryExists) {
      console.log(`Directory ${remotePath} does not exist, creating it`);
      await this.sambaClient.mkdir(remotePath);
    }
    console.log(`Directory ${remotePath} exists, writing file ${filename}`);
    const buffer = data.get();
    const tempFilename = `${uuid()}.tmp`;
    const localFile = `${this.sambaConfig.tmpDirectory}/${tempFilename}`;
    console.log(`Writing file to ${localFile}`);
    fs.writeFileSync(localFile, new Uint8Array(buffer));
    let success = false;
    try {
      const path = remotePath === '' ? filename : `${remotePath}/${filename}`;
      console.log(`Sending local file ${localFile} to ${path}`);
      const feedback = await this.sambaClient.sendFile(localFile, path);
      console.log('feedback', feedback);
      console.log(
        'feedback includes NT_STATUS_OK',
        feedback.includes('NT_STATUS_OK'),
      );
      success = true;
    } catch (e) {
      console.log(e);
    } finally {
      fs.unlinkSync(localFile);
    }
    return success;
  }

  async deleteFile(remoteFile: string): Promise<boolean> {
    try {
      const feedback = await this.sambaClient.deleteFile(remoteFile);
    } catch (e) {
      console.log(e);
      return false;
    }

    return true;
  }

  fileExists(location: string): Promise<boolean> {
    return this.sambaClient.fileExists(location);
  }
}
